import React, { useRef } from "react";

export default function TodoForm({ todo, setTodo, fetchData, titleRef }) {
  const onSubmitHandler = async (e) => {
    e.preventDefault();
    let url = "https://ancient-scrubland-64264.herokuapp.com/api/";
    if (todo.editing_id !== null) {
      url = `https://ancient-scrubland-64264.herokuapp.com/api/${todo.editing_id}/task`;
    }
    await fetch(url, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ title: titleRef.current.value }),
    });
    fetchData();
    titleRef.current.value = "";
    if (todo.editing_id !== null) {
      setTodo((prev) => ({ ...prev, editing_id: null }));
    }
  };

  return (
    <div id="form-wrapper">
      <form id="form" onSubmit={(e) => onSubmitHandler(e)}>
        <div className="flex-wrapper">
          <div style={{ flex: 6 }}>
            <input
              id="title"
              className="form-control"
              type="text"
              name="title"
              placeholder="Add task"
              ref={titleRef}
            />
          </div>
          <div style={{ flex: 1 }}>
            <input id="submit" className="btn" type="submit" />
          </div>
        </div>
      </form>
    </div>
  );
}
