import React from "react";

export default function TodoList({ todo, titleRef, setTodo, fetchData }) {
  const onClickHandler = async (task) => {
    await fetch(
      `https://ancient-scrubland-64264.herokuapp.com/api/${task.id}/task`,
      {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ title: task.title, completed: !task.completed }),
      }
    );
    fetchData();
  };

  const editHandler = (task) => {
    setTodo((prev) => ({ ...prev, editing_id: task.id }));
    titleRef.current.value = task.title;
  };

  const deleteHandler = async (task) => {
    await fetch(
      `https://ancient-scrubland-64264.herokuapp.com/api/${task.id}/task`,
      {
        method: "DELETE",
        headers: {
          "Content-Type": "application/json",
        },
      }
    );
    setTodo((prev) => ({ ...prev, editing_id: null }));
    fetchData();
  };

  return (
    <div id="list-wrapper">
      {todo.todoList.map((task) => (
        <div key={task.id} className="task-wrapper flex-wrapper">
          <div onClick={() => onClickHandler(task)} style={{ flex: 7 }}>
            {task.completed ? (
              <strike>{task.title}</strike>
            ) : (
              <span>{task.title}</span>
            )}
          </div>
          <div style={{ flex: 1 }}>
            <button
              onClick={() => editHandler(task)}
              className="btn btn-sm btn-outline-info"
            >
              Edit
            </button>
          </div>
          <div style={{ flex: 1 }}>
            <button
              onClick={() => deleteHandler(task)}
              className="btn btn-sm btn-outline-dark"
            >
              Delete
            </button>
          </div>
        </div>
      ))}
    </div>
  );
}
