import "./App.css";
import React, { useState, useEffect, useRef } from "react";
import TodoForm from "./components/TodoForm";
import TodoList from "./components/TodoList";

function App() {
  const title = useRef();

  const [Todo, setTodo] = useState({
    todoList: [],
    editing_id: null,
  });

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    let data = await fetch(
      "https://ancient-scrubland-64264.herokuapp.com/api/"
    );
    data = await data.json();
    setTodo((prev) => ({ ...prev, todoList: data }));
  };

  return (
    <div className="container">
      <div id="task-container">
        <TodoForm
          todo={Todo}
          setTodo={setTodo}
          fetchData={fetchData}
          titleRef={title}
        />
        <TodoList
          todo={Todo}
          titleRef={title}
          setTodo={setTodo}
          fetchData={fetchData}
        />
      </div>
    </div>
  );
}

export default App;
